@import '../common/styles/sass/variables';
@import '../common/styles/sass/mixins';

.projectsBlock {
    @include block($bgColor);

    .projectsContainer {
        justify-content: center;

        .projects {
            @include blockInside(space-between);
            @media screen and (max-width: 1129px) {
                justify-content: center;
            }
        }
    }


}